<template>
  <div class="new-list1">
      <table class="table pulbic-table">
        <thead>
          <tr>
            <td>{{ $t('translateLang.tradeDetail.text37') }}</td>
            <td>{{ $t('translateLang.tradeDetail.text39') }}</td>
            <td>{{ $t('translateLang.tradeDetail.text40') }}</td>
          </tr>
        </thead>

        <tbody  id="realtime-trade-sell" v-if="plateData.trade_plate">
          <tr v-for="item in  plateData.trade_plate.trade_sell" :key="item.id" ><td class="c-red">{{ $t('tradeDetail.newList.part7')  }}{{ item.order_id_sell }}</td><td class="c-red">{{ parseFloat(item.price).toFixed(5) }}</td><td>{{ item.trade_num_sell }}</td></tr>
          <tr v-for="item in sellDataNum" :key="item.id" class="t-center"><td>-</td> <td>-</td> <td>-</td></tr>
          <!-- <tr v-if="plateData.trade_plate.trade_sell.length < 16" v-for="item in plateData.trade_plate.trade_sell" :key="item.id" ><td>-</td><td>-</td><td>-</td></tr> -->
        </tbody>

        <tbody class="more-plant">
          <tr>
            <td colspan="3">
              <div class="clearfix">
                <span class="pull-left c-616" v-if="$i18n.locale == 'zn'" ><span class="c-red" >{{ headInfo.last_price }}</span>  ≈ {{ (headInfo.last_price * headInfo.exchange_cny_price).toFixed(2) }} CNY </span>
                <span class="pull-left c-616" v-if="$i18n.locale == 'zh-hk'"><span class="c-red" >{{ headInfo.last_price }}</span>  ≈ HK${{ (headInfo.last_price * headInfo.exchange_hkd_price).toFixed(2) }} </span>
                <span class="pull-left c-616" v-if="$i18n.locale == 'en'"><span class="c-red" >{{ headInfo.last_price }}</span>  ≈ ${{ (headInfo.last_price * headInfo.exchange_usd_price).toFixed(2) }} </span>

                <a @click="toTradePlant" href="javascript:void(0)" class="more-plant-link pull-right" >{{ $t('translateLang.tradeDetail.text41') }}</a>
              </div> 
            </td>
          </tr>
        </tbody>
        
        <tbody id="realtime-trade-by">
          <tr v-for="item in plateData.trade_plate.trade_buy" :key="item.id"><td class="c-green">{{ $t('tradeDetail.newList.part8')  }}{{ item.order_id_buy }}</td><td class="c-green">{{ parseFloat(item.price).toFixed(5) }}</td><td>{{ item.trade_num_buy }}</td></tr>
          <tr v-for="item in buyDataNum" :key="item.id" class="t-center"><td>-</td> <td>-</td> <td>-</td></tr>
        </tbody>
      </table>
  </div>
</template>

<script>
export default {
  props: ['plateData', 'tradeInfo', 'headInfo'],
  data() {
    return {
    }
  },
  computed: {
    // sellData() {
    //   for (let i = 0; i < 15; i++) {
    //     return this.plateData.trade_plate.trade_sell
    //   }
    // },
    // buyData() {
    //   return this.plateData.trade_plate.trade_buy
    // }
    sellDataNum() {
      const num = []
      const count = this.plateData.trade_plate.trade_sell.length ? this.plateData.trade_plate.trade_sell.length : 0
      console.log(this.plateData)
      for (let i = 0; i < (10 - count); i++) {
        num.push(i)
      }
      return num
    },
    buyDataNum() {
      const num = []
      const count = this.plateData.trade_plate.trade_buy.length ? this.plateData.trade_plate.trade_buy.length : 0

      for (let i = 0; i < (10 - count); i++) {
        num.push(i)
      }
      return num
    }
  },
  methods: {
    toTradePlant() {
      this.$router.push({ name: 'tradePlant', query: { 'trade_area_id': this.$route.query.trade_area_id, 'currency_id': this.$route.query.currency_id, 'currency_mark': this.$route.query.currency_mark, 'currency_name': this.$route.query.currency_name }})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .new-list1 {
    font-size: 12px;
    background: #191b2b !important;
    overflow: hidden;
    height: 764px;
    .pulbic-table thead tr {
        // background: #2a314e;
        color: #828594;
        line-height: 1.42857143;
    }

    .pulbic-table tr td:first-child {
        padding: 0px 20px !important;
    }
    .pulbic-table tr td:last-child {
        padding: 0px 20px !important;
        text-align: right;
    }
    .pulbic-table tr td {
        vertical-align: middle;
        text-align: left;
        line-height: 32px;
    }
    .pulbic-table {
        text-align: left;
        font-size: 12px;
    }
    #realtime-trade-sell {
        border-bottom: 1px solid #191b2a;
    }
    .pulbic-table>tbody tr:hover{
      // background: #2a314e;
    }
    table {
      border-spacing: 0;
      border-collapse: collapse;
      width: 100%;
      max-width: 100%;
      margin-bottom: 20px;
    }
    .pulbic-table tbody tr td{
      color: #b0b8db;
    }
    .t-center td{
      text-align: center !important;
    }
    .more-plant{
       tr td{
        padding-left: 0;
        padding-right: 0;
        line-height: 43px;
        border-top: #0d0f17 1px solid;
        border-bottom: #0d0f17 1px solid;
        color: #61688a;
        span.c-red{
          font-size: 18px;
        }
        .more-plant-link{
          color: #1fa8f6;
        }
      }
    }
  }
</style>



